.btn {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
.btn__text {
  padding: 0 8px;
  font-weight: 500;
  letter-spacing: -0.2px;
  transition: color 0.6s ease;
}
.btn svg {
  width: 40px;
  height: 40px;
  color: #fff;
  fill: #ff3e79;
  stroke: #ff3e79;
  stroke-width: 2px;
  transition: color 0.6s ease, fill 0.6s ease;
}
.btn--circle {
  border-radius: 50%;
}
.btn--rounded {
  border-radius: 20px;
}
.btn--expand {
  border-radius: 2px;
}
.btn--expand svg {
  width: 24px;
  height: 24px;
  color: #41f6d7;
  transition: transform 0.6s ease, color 0.6s ease;
}
.btn--expand .btn__text {
  display: inline-block;
}
.btn--expand[aria-expanded=true] svg {
  transform: rotate(180deg);
}
.btn--expand[aria-expanded=false] svg {
  transform: rotate(0deg);
}
.btn--go, .btn--text-go {
  width: 100%;
  min-width: 200px;
  min-height: 72px;
  padding: 0 16px;
  background: #fff;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  transition: border-color 0.6s ease;
}
.btn--go svg, .btn--text-go svg {
  margin-left: auto;
  color: #ff3e79;
  fill: #fff;
}
.btn--text-go {
  flex-direction: column;
  min-height: 160px;
  padding: 16px;
}
.btn--text-go .btn__text {
  align-self: flex-start;
  padding: 0;
}
.btn--text-go svg {
  margin-top: auto;
}
.btn--action {
  padding: 4px;
  color: #fff;
  background-color: #ff3e79;
  border-radius: 16px;
}
.btn--action .btn__text {
  position: relative;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
}
.btn--action .btn__text::after {
  content: "";
  position: absolute;
  bottom: 4px;
  left: 8px;
  transform-origin: bottom right;
  transform: scaleX(0);
  width: 80%;
  height: 2px;
  background-color: #fff;
  transition: transform 0.6s ease;
}
.btn--action svg {
  width: 24px;
  height: 24px;
  margin-left: auto;
}
.btn:disabled {
  pointer-events: none;
  opacity: 0.5;
}
.btn:active {
  filter: brightness(0.95);
}
@media (hover: hover) and (pointer: fine) {
  .btn:hover {
    color: #ff3e79;
  }
  .btn:hover svg {
    color: #ff3e79;
    fill: #fff;
  }
  .btn:hover.btn--go, .btn:hover.btn--text-go {
    border-color: #b8ffec;
  }
  .btn:hover.btn--go svg, .btn:hover.btn--text-go svg {
    color: #fff;
    fill: #ff3e79;
  }
  .btn:hover.btn--action {
    color: #fff;
  }
  .btn:hover.btn--action .btn__text::after {
    transform-origin: bottom left;
    transform: scaleX(1);
  }
}

.link {
  display: inline-block;
  border-radius: 2px;
  transition: color 0.6s ease, background-color 0.6s ease;
}
