.modal {
  pointer-events: none;
  scrollbar-width: none;
  position: fixed;
  z-index: 1000;
  inset: 0;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(255, 255, 255, 0.96);
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100%;
}
.modal__overlay {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.modal__content {
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  overflow-y: auto;
  min-width: 320px;
  max-width: 100%;
  height: 100%;
  padding: 106px 16px 24px;
  background-color: #fff;
  box-shadow: 16px 0 52px rgba(14, 24, 80, 0.2);
  transition: max-width 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
}
@media (min-width: 360px) {
  .modal__content {
    padding: 106px 24px 24px;
  }
}
@media (min-width: 768px) {
