.page-content {
  background-color: $color-white-smoke;
  border-top: 1px solid $color-clear-day;

  &__title {
    position: relative;

    max-width: 600px;
    margin: 0;
    padding: 24px 16px;

    font-size: 28px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -0.6px;

    &::before {
      content: "";

      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);

      width: 4px;
      height: 40px;

      background: $color-turquoise;
      border-radius: 0 4px 4px 0;
    }
  }

  &__section-title {
    margin: 0;
    padding: 16px;

    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: $color-storm-grey;
    text-transform: uppercase;

    background: $white-smoke-to-white;
    border-top: 1px solid $color-clear-day;
  }

  &__about {
    margin-bottom: 32px;
  }

  @media (min-width: $vp-mobile) {
    &__title {
      padding: 24px;
    }

    &__section-title {
      padding: 16px 24px;
    }
  }

  @media (min-width: $vp-desktop) {
    border-top: none;

    &__title {
      padding: 40px 32px;

      &::before {
        left: 2px;
        width: 3px;
        border-radius: 2px;
      }
    }

    &__section-title {
      padding: 16px 32px;
    }
  }
}
